<template>
    <div class="notices-line">
        <div class="row">
            <div class="wrap clearfix">
                <span class="title"></span>
                <div class="notices-wrapper">
                    <div class="notices-scroll clearfix">
                        <ul class="notices-list">
                            <li class="notice-item"><a href="javascript:;"></a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    methods: {
        ScrollLeft: function() {
            let speed = 30;
            let timer = null;
            let noticeWrap = $('.notices-scroll');
            let noticeMove = $('.notices-list');
            let ww = noticeWrap.width();
            let iw = noticeMove.width();
            let moveLeft = 0;
            let move = () => {
                if (moveLeft > ww) {
                    moveLeft = -iw;
                } else {
                    moveLeft++;
                }
                noticeMove.css('right', moveLeft);
            }
            timer = setInterval(move, speed);
            noticeWrap.on('mouseenter',function (){
                clearInterval(timer);
            })
            .on('mouseleave',function (){
                timer = setInterval(move, speed);
            })
        }
    },
    mounted() {
        this.ScrollLeft();
    }
}
</script>

<style lang="scss" scoped>
.notices-line {
    height: 40px;
    line-height: 40px;
    background: -prefix-linear-zgradient(top, #f3f3f3, #d7d7d7); 
    background: linear-gradient(to bottom, #f3f3f3, #d7d7d7);
    .title {
        display: block;
        position: absolute;
        font-size: 14px;
        font-weight: bold;
        height: 40px;
        left: 0;
        top: 0;
    }
    .notices-wrapper {
        padding-left: 100px;
        padding-right: 30px;
        height: 40px;
        width: 100%;
        .notices-scroll {
            position: relative;
            height: 100%;
            width: 100%;
            overflow: hidden;
            .notices-list {
                position: absolute;
                right: 0;
                .notice-item {
                    float: left;
                    white-space: nowrap;
                    a {
                        color: #990000;
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
</style>

