<template>
    <div class="banner-swiper">
        <swiper :options="swiperOption">
            <!-- slides -->
            <swiper-slide class="slide-1"></swiper-slide>
            <swiper-slide class="slide-2"></swiper-slide>
            <swiper-slide class="slide-3"></swiper-slide>
            <swiper-slide class="slide-4"></swiper-slide>
            <swiper-slide class="slide-5"></swiper-slide>
            <!-- Optional controls -->
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
    import 'swiper/dist/css/swiper.css'
    import { swiper, swiperSlide } from 'vue-awesome-swiper'

    export default {
        data() {
            return {
                swiperOption: {
                    spaceBetween: 30,
                    loop: true,
                    loopedSlides: 5,
                    autoplay: {
                        delay: 5000,
                        stopOnLastSlide: false,
                        disableOnInteraction: true,
                    },
                    effect: 'fade',
                    pagination: {
                        el: '.swiper-pagination',
                        clickable: true
                    }
                }
            }
        },
        components: {
            swiper,
            swiperSlide
        }
    }
</script>
<style lang="scss" scoped>
    .banner-swiper {
        width: 100%;
        height: 400px;
        .swiper-container {
            height: 100%;
            width: 100%;
        }
    }
    .swiper-slide {
        background-position: center;
        background-size: cover;
        &.slide-1 {
            background-image:url('../../common/img/banner/banner-home-1.jpg')
        }
        &.slide-2 {
            background-image:url('../../common/img/banner/banner-home-2.jpg')
        }
        &.slide-3 {
            background-image:url('../../common/img/banner/banner-home-3.jpg')
        }
        &.slide-4 {
            background-image:url('../../common/img/banner/banner-home-4.jpg')
        }
        &.slide-5 {
            background-image:url('../../common/img/banner/banner-home-5.jpg')
        }
    }
</style>
